<template>
  <div class="purchase bg-primary">
    <div class="search-frame fixed-top px-2 py-4 searchgbar bg-secondary d-flex justify-content-between">
      <img src="../assets/images/profileIcon.png" alt="" height="30px">
      <p class="mb-0 h5 text-white d-flex align-items-center">結帳</p>
      <img src="../assets/images/backhome.png" alt="" height="30px" @click="$router.push({ name: 'Cart' })">
    </div>
    <div class="purchase-content p-3">
      <div class="purchase-list bg-white mb-3">
        <div v-for="(item,index) in cartlist" :key="index" class="border-bottom border-gray p-2 d-flex">
            <img :src="item.img" class="w-25 h-25 img-fluid mr-2" alt="">
            <div class="w-100 position-relative">
              <p class="mb-0 text-left text-secondary"> {{ item.name }}</p>
              <div class="purchase-list-text w-100 position-absolute justify-content-between d-flex">
                <p class="mb-0 text-danger">
                  <span :class="{'discount-text':item.discount}">${{ item.price | currency }}</span>
                  <span v-if="item.discount">${{ item.discount | currency }}</span></p>
                <p class="mb-0">x{{ item.count }}</p>
              </div>
            </div>
        </div>
        <div class="p-2 text-danger text-right">
          <b class="mb-0">總金額：${{ totalMoney | currency }}</b>
        </div>
      </div>
      <div class="purchase-way p-3 bg-white text-left text-secondary mb-3">
        <form>
          <p class="text-danger">付款方式</p>
          <input type="radio" v-model="payway" id="cash" value="現金">
          <label class="ml-2" for="cash">現金</label>
          <br>
          <input type="radio" v-model="payway" id="credit" value="信用卡">
          <label class="ml-2" for="credit">信用卡</label>
          <br>
          <p class="text-danger">運送方式</p>
          <input type="radio" v-model="deliver" id="home" value="宅配">
          <label class="ml-2" for="home">宅配</label>
          <br>
          <input type="radio" v-model="deliver" id="store" value="超商取貨">
          <label class="ml-2" for="store">超商取貨</label>
        </form>
      </div>
      <div class="recipient p-3 bg-white text-left text-secondary mb-3">
        <p class="text-danger">收件人資訊</p>
        <p v-show="recipient" class="text-danger h7">*請務必填寫</p>
        <form>
          <p>姓名：<input type="text" v-model="recipientname"></p>
          <p>電話：<input type="tel" v-model="phonenumber" maxlength="10"></p>
          <p>信箱：<input type="email" v-model="email"></p>
          <p>地址：<input type="text" v-model="address"></p>
        </form>
      </div>
      <div class="d-flex justify-content-center align-items-center">
        <button class="border border-secondary px-4 py-3 mb-0 text-secondary" @click="checkpurchase">去結帳</button>
      </div>
    </div>
    <div v-if="recipientdetail" class="recipient-content position-fixed d-flex justify-content-center align-items-center">
      <div class="recipient-detail position-relative p-6 bg-white w-75 text-left">
        <img class="close-btn position-absolute" src="../assets/images/btn_close.png" alt="" @click="recipientdetail = false">
        <p class="text-center h5 text-danger">配送明細</p>
        <p>金額： <span class="text-danger">${{ totalMoney | currency }}</span></p>
        <p>付款方式： {{  payway }}</p>
        <p>運送方式： {{ deliver}}</p>
        <p>收件人：{{ recipientname }}</p>
        <p>電話： {{ phonenumber }}</p>
        <p class="w-100 text-break">信箱：{{ email }}</p>
        <p class="w-100 text-break">地址： {{ address }} </p>
        <div class="d-flex justify-content-center">
          <button type="button" class="btn btn-outline-secondary" @click="submit">確認送出</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'Purchase',
  data () {
    return {
      payway: '現金',
      deliver: '宅配',
      recipientname: '',
      phonenumber: '',
      email: '',
      address: '',
      recipient: false,
      recipientdetail: false

    }
  },
  computed: {
    ...mapState({
      cartlist: state => state.cartlist
    }),
    totalMoney () {
      const money = this.cartlist.map(item => {
        return item.count * (item.discount ? item.discount : item.price)
      })
      const total = money.reduce((val, cur) => {
        return val + cur
      }, 0)
      return total
    }
  },
  filters: {
    currency (price) {
      return price.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
    }
  },
  methods: {
    checkpurchase () {
      if (this.recipientname === '' || this.phonenumber === '' || this.email === '' || this.address === '') {
        this.recipient = true
      } else {
        this.recipientdetail = true
      }
    },
    submit () {
      alert('已為您送出訂單')
      this.$store.commit('submitCarlist')
      this.$router.push({ name: 'Store' })
    }
  }
}
</script>
